// Esse reducer ser√° respons√°vel por tratar as informa√ß√µes da pessoa usu√°ria
import { USER_EMAIL } from '../actions';

const INITIAL_STATE = {
  email: '',
};

const userReducer = (state = INITIAL_STATE, action) => {
  switch (action.type) {
  case USER_EMAIL:
    return {
      email: action.email,
    };
  default:
    return state;
  }
};

export default userReducer;
